import React, { Component } from 'react';

class Home extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Welcome to my Portfolio !</h1>
        </header>
      </div>
    );
  }
}

export default Home;



